/**
 * Vendor-specific overrides and settings
 */
dialog {
  box-sizing: border-box;
  position: absolute;
  left: 0;
  right: 0;
  width: fit-content;
  height: fit-content;
  margin: auto;
  border: solid;
  padding: 1em;
  background: white;
  color: black;
  display: block; }

dialog:not([open]) {
  display: none; }

dialog + .backdrop {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.25); }

._dialog_overlay {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0; }

dialog.fixed {
  position: fixed;
  top: 50%;
  transform: translate(0, -50%); }

.gform_fields {
  list-style-type: none;
  margin: 0;
  padding-left: 0; }

.gform_body {
  width: 100%; }
  .gform_body .gform_fields {
    display: flex;
    flex-wrap: wrap; }

.ginput_container {
  margin: 0 auto;
  max-width: 100%; }
  .ginput_container .medium,
  .ginput_container .large {
    width: 100%; }

.gform_validation_container {
  display: none; }

/* Special classes and settings for the lazysizes JS library */
.lazyload--blurUp {
  filter: blur(0.5rem);
  transition: filter 100ms ease; }
  .lazyload--blurUp.lazyloaded {
    filter: blur(0); }

html.pushbar_locked {
  overflow: hidden;
  touch-action: none; }

.pushbar_locked .pushbar_main_content.pushbar_blur {
  filter: blur(1rem); }

.pushbar {
  z-index: 1000;
  position: fixed;
  overflow-y: auto;
  transition: transform 300ms ease;
  max-width: 100%;
  width: 20rem;
  will-change: transform; }

.pushbar_overlay {
  z-index: -999;
  position: fixed;
  width: 100%;
  max-width: 100%;
  height: 100%;
  min-height: 100vh;
  top: 0;
  left: 0;
  will-change: opacity;
  transition: opacity 300ms ease;
  opacity: 0;
  background: rgba(0, 0, 0, 0.25); }

html.pushbar_locked .pushbar_overlay {
  opacity: .8;
  z-index: 1;
  transition: opacity 300ms ease; }

.pushbar.from_left {
  top: 0;
  left: 0;
  height: 100%;
  min-height: 100vh;
  transform: translateZ(0) translateX(-100%); }

.pushbar.from_right {
  top: 0;
  right: 0;
  height: 100%;
  min-height: 100vh;
  transform: translateZ(0) translateX(100%); }

.pushbar.from_top {
  top: 0;
  left: 0;
  min-height: 9rem;
  transform: translateZ(0) translateY(-100%); }

.pushbar.from_bottom {
  bottom: 0;
  left: 0;
  min-height: 9rem;
  transform: translateZ(0) translateY(100%); }

.pushbar.opened {
  display: block;
  transform: translateX(0) translateY(0); }

/* WordPress Core */
.alignnone,
a img.alignnone,
.alignleft,
a img.alignleft,
.wp-caption.alignnone,
.wp-caption.alignleft {
  margin: 5px 20px 20px 0; }

.aligncenter,
div.aligncenter,
a img.aligncenter {
  display: block;
  margin: 5px auto 0; }

.alignright,
a img.alignright {
  float: right;
  margin: 5px 0 20px 20px; }

.alignleft,
a img.alignleft {
  float: left; }

.wp-caption {
  background: #fff;
  border: 1px solid #f0f0f0;
  max-width: 96%;
  padding: 5px 3px 10px;
  text-align: center; }

.wp-caption.alignright {
  margin: 5px 0 20px 20px; }

.wp-caption img {
  border: 0 none;
  box-sizing: border-box;
  height: auto;
  margin: 0;
  max-width: 98.5%;
  padding: 0;
  width: auto; }

.wp-caption p.wp-caption-text {
  font-size: 11px;
  line-height: 17px;
  margin: 0;
  padding: 0 4px 5px; }

/* Text meant only for screen readers. */
.screen-reader-text {
  border: 0;
  box-sizing: border-box;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
  /* Many screen reader and browser combinations announce broken words as they would appear visually. */ }

.screen-reader-text:focus {
  background-color: #eee;
  box-sizing: border-box;
  clip: auto !important;
  clip-path: none;
  color: #444;
  display: block;
  font-size: 1em;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */ }

@media only screen and (min-width: 768px) {
  .alignfull {
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
    width: auto;
    max-width: 1000%; }
  .alignwide {
    margin-left: calc(25% - 25vw);
    margin-right: calc(25% - 25vw);
    width: auto;
    max-width: 1000%; }
  .alignwide img,
  .alignfull img {
    display: block;
    margin: 0 auto; } }
